<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="活动名称" prop="huodongmingcheng">
							<el-input class="list_inp" v-model="form.huodongmingcheng" placeholder="活动名称"
								 type="text" 								:readonly="!isAdd||disabledForm.huodongmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="图片" prop="tupian">
							<uploads
								:disabled="!isAdd||disabledForm.tupian?true:false"
								action="file/upload" 
								tip="请上传图片" 
								:limit="3" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.tupian?form.tupian:''" 
								@change="tupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="活动分类" prop="huodongfenlei">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.huodongfenlei?true:false"
								v-model="form.huodongfenlei" 
								placeholder="请选择活动分类"
								>
								<el-option v-for="(item,index) in huodongfenleiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="活动时间" prop="huodongshijian">
							<el-date-picker
								class="list_date"
								v-model="form.huodongshijian"
								format="YYYY 年 MM 月 DD 日"
								value-format="YYYY-MM-DD"
								type="datetime"
								:readonly="!isAdd||disabledForm.huodongshijian?true:false"
								placeholder="请选择活动时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="活动地点" prop="huodongdidian">
							<el-input class="list_inp" v-model="form.huodongdidian" placeholder="活动地点"
								 type="text" 								:readonly="!isAdd||disabledForm.huodongdidian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="发表时间" prop="fabiaoshijian">
							<el-date-picker
								class="list_date"
								v-model="form.fabiaoshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.fabiaoshijian?true:false"
								placeholder="请选择发表时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="截止时间" prop="jiezhishijian">
							<el-date-picker
								class="list_date"
								v-model="form.jiezhishijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.jiezhishijian?true:false"
								placeholder="请选择截止时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="报名名额" prop="baomingminge">
							<el-input class="list_inp" v-model.number="form.baomingminge" placeholder="报名名额"
								 type="text" 								:readonly="!isAdd||disabledForm.baomingminge?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="主办方账号" prop="zhubanfangzhanghao">
							<el-input class="list_inp" v-model="form.zhubanfangzhanghao" placeholder="主办方账号"
								 type="text" 								:readonly="!isAdd||disabledForm.zhubanfangzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="主办方名称" prop="zhubanfangmingcheng">
							<el-input class="list_inp" v-model="form.zhubanfangmingcheng" placeholder="主办方名称"
								 type="text" 								:readonly="!isAdd||disabledForm.zhubanfangmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="活动星级" prop="huodongxingji">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.huodongxingji?true:false"
								v-model="form.huodongxingji" 
								placeholder="请选择活动星级"
								>
								<el-option v-for="(item,index) in huodongxingjiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="不适合人群" prop="bushiherenqun">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.bushiherenqun?true:false"
								v-model="form.bushiherenquns" 
								placeholder="请选择不适合人群"
								multiple
								@change="huodongxinxibushiherenqunChange">
								<el-option v-for="(item,index) in bushiherenqunLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="报名须知" prop="baomingxuzhi">
							<el-input v-model="form.baomingxuzhi" placeholder="报名须知" type="textarea"
							:readonly="!isAdd||disabledForm.baomingxuzhi?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="活动内容" prop="huodongneirong">
							<editor :value="form.huodongneirong" placeholder="请输入活动内容" :readonly="!isAdd||disabledForm.huodongneirong?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'huodongneirong')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'huodongxinxi'
	const formName = '活动信息'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		huodongmingcheng : false,
		tupian : false,
		huodongfenlei : false,
		huodongshijian : false,
		huodongdidian : false,
		huodongneirong : false,
		baomingxuzhi : false,
		fabiaoshijian : false,
		jiezhishijian : false,
		baomingminge : false,
		storeupnum : false,
		clicknum : false,
		zhubanfangzhanghao : false,
		zhubanfangmingcheng : false,
		huodongxingji : false,
		sfsh : false,
		shhf : false,
		clicktime : false,
		bushiherenqun : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		huodongmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		tupian: [
		],
		huodongfenlei: [
		],
		huodongshijian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		huodongdidian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		huodongneirong: [
		],
		baomingxuzhi: [
		],
		fabiaoshijian: [
		],
		jiezhishijian: [
		],
		baomingminge: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicknum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		zhubanfangzhanghao: [
		],
		zhubanfangmingcheng: [
		],
		huodongxingji: [
		],
		sfsh: [
		],
		shhf: [
		],
		clicktime: [
		],
		bushiherenqun: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//图片上传回调
	const tupianUploadSuccess=(e)=>{
		form.value.tupian = e
	}
	//活动分类列表
	const huodongfenleiLists = ref([])
	//活动星级列表
	const huodongxingjiLists = ref([])
	//不适合人群列表
	const bushiherenqunLists = ref([])
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			huodongmingcheng: '',
			tupian: '',
			huodongfenlei: '',
			huodongshijian: '',
			huodongdidian: '',
			huodongneirong: '',
			baomingxuzhi: '',
			fabiaoshijian: '',
			jiezhishijian: '',
			baomingminge: '',
			storeupnum: '0',
			clicknum: '0',
			zhubanfangzhanghao: '',
			zhubanfangmingcheng: '',
			huodongxingji: '',
			sfsh: '待审核',
			shhf: '',
			clicktime: '',
			bushiherenqun: [],
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.huodongneirong = res.data.data.huodongneirong?(res.data.data.huodongneirong.replace(reg,'../../../cl92110163/file')):'';
			if(res.data.data.bushiherenqun){
				res.data.data.bushiherenquns = res.data.data.bushiherenqun.split(",")
			}else{
				res.data.data.bushiherenquns = []
			}
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.fabiaoshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='huodongmingcheng'){
					form.value.huodongmingcheng = row[x];
					disabledForm.value.huodongmingcheng = true;
					continue;
				}
				if(x=='tupian'){
					form.value.tupian = row[x];
					disabledForm.value.tupian = true;
					continue;
				}
				if(x=='huodongfenlei'){
					form.value.huodongfenlei = row[x];
					disabledForm.value.huodongfenlei = true;
					continue;
				}
				if(x=='huodongshijian'){
					form.value.huodongshijian = row[x];
					disabledForm.value.huodongshijian = true;
					continue;
				}
				if(x=='huodongdidian'){
					form.value.huodongdidian = row[x];
					disabledForm.value.huodongdidian = true;
					continue;
				}
				if(x=='huodongneirong'){
					form.value.huodongneirong = row[x];
					disabledForm.value.huodongneirong = true;
					continue;
				}
				if(x=='baomingxuzhi'){
					form.value.baomingxuzhi = row[x];
					disabledForm.value.baomingxuzhi = true;
					continue;
				}
				if(x=='fabiaoshijian'){
					form.value.fabiaoshijian = row[x];
					disabledForm.value.fabiaoshijian = true;
					continue;
				}
				if(x=='jiezhishijian'){
					form.value.jiezhishijian = row[x];
					disabledForm.value.jiezhishijian = true;
					continue;
				}
				if(x=='baomingminge'){
					form.value.baomingminge = row[x];
					disabledForm.value.baomingminge = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicknum'){
					form.value.clicknum = row[x];
					disabledForm.value.clicknum = true;
					continue;
				}
				if(x=='zhubanfangzhanghao'){
					form.value.zhubanfangzhanghao = row[x];
					disabledForm.value.zhubanfangzhanghao = true;
					continue;
				}
				if(x=='zhubanfangmingcheng'){
					form.value.zhubanfangmingcheng = row[x];
					disabledForm.value.zhubanfangmingcheng = true;
					continue;
				}
				if(x=='huodongxingji'){
					form.value.huodongxingji = row[x];
					disabledForm.value.huodongxingji = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='bushiherenqun'){
					form.value.bushiherenqun = row[x];
					disabledForm.value.bushiherenqun = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.clicknum='0'
			form.value.sfsh='待审核'
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(context?.$toolUtil.storageGet("role")!="管理员") {
				disabledForm.value.huodongxingji = true;
			}
		})
		context?.$http({
			url: `option/huodongfenlei/huodongfenlei`,
			method: 'get'
		}).then(res=>{
			huodongfenleiLists.value = res.data.data
		})
		huodongxingjiLists.value = "一星,二星,三星,四星,五星".split(',')
		context?.$http({
			url: `option/bushiherenqun/bushiherenqun`,
			method: 'get'
		}).then(res=>{
			bushiherenqunLists.value = res.data.data
		})
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	const huodongxinxibushiherenqunChange= ()=> {
		form.value.bushiherenqun = form.value.bushiherenquns.join(',')
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.tupian!=null) {
			form.value.tupian = form.value.tupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border: 0px solid #ddd;
		border-radius: 4px;
		padding: 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 150px 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: 150px;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
