	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import quxiaobaoming from '@/views/quxiaobaoming/list'
	import zhubanfang from '@/views/zhubanfang/list'
	import huodongfenlei from '@/views/huodongfenlei/list'
	import biaoqian from '@/views/biaoqian/list'
	import huodongxinxi from '@/views/huodongxinxi/list'
	import discusshuodongxinxi from '@/views/discusshuodongxinxi/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import yonghu from '@/views/yonghu/list'
	import huodongbaoming from '@/views/huodongbaoming/list'
	import bushiherenqun from '@/views/bushiherenqun/list'
	import config from '@/views/config/list'
	import zhubanfangRegister from '@/views/zhubanfang/register'
	import zhubanfangCenter from '@/views/zhubanfang/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/zhubanfangCenter',
			name: '主办方个人中心',
			component: zhubanfangCenter
		}
		,{
			path: '/news',
			name: '通知公告',
			component: news
		}
		,{
			path: '/quxiaobaoming',
			name: '取消报名',
			component: quxiaobaoming
		}
		,{
			path: '/zhubanfang',
			name: '主办方',
			component: zhubanfang
		}
		,{
			path: '/huodongfenlei',
			name: '活动分类',
			component: huodongfenlei
		}
		,{
			path: '/biaoqian',
			name: '标签',
			component: biaoqian
		}
		,{
			path: '/huodongxinxi',
			name: '活动信息',
			component: huodongxinxi
		}
		,{
			path: '/discusshuodongxinxi',
			name: '活动信息评论',
			component: discusshuodongxinxi
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/huodongbaoming',
			name: '活动报名',
			component: huodongbaoming
		}
		,{
			path: '/bushiherenqun',
			name: '不适合人群',
			component: bushiherenqun
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
	{
		path: '/zhubanfangRegister',
		name: '主办方注册',
		component: zhubanfangRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
